.logo {
  width: 48px;
  height: auto;
}
@media screen and (max-width: 767.98px) {
  .logo {
    width: 58px;
  }
}
@media screen and (min-width: 768px) {
  .logo {
    width: 68px;
  }
}
@media screen and (min-width: 1280px) {
  .logo {
    width: 88px;
  }
}

.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--dark-bg-color);
}

.navigationList {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.navigationItem {
  padding-top: 10px;
  padding-bottom: 10px;
}
@media screen and (min-width: 768px) {
  .navigationItem {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
@media screen and (min-width: 1280px) {
  .navigationItem {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}

.navigationItem:first-child {
  margin-left: 5px;
}
@media screen and (min-width: 768px) {
  .navigationItem:first-child {
    margin-left: 10px;
  }
}
@media screen and (min-width: 1280px) {
  .navigationItem:first-child {
    margin-left: 25px;
  }
}
@media screen and (min-width: 480px) {
  .navigationItem:not(:last-child) {
    margin-right: 30px;
  }
}

@media screen and (min-width: 768px) {
  .navigationItem:not(:last-child) {
    margin-right: 70px;
  }
}

@media screen and (min-width: 1280px) {
  .navigationItem:not(:last-child) {
    margin-right: 140px;
  }
}
@media screen and (min-width: 480px) {
  .navigationItem:last-child {
    margin-right: 30px;
  }
}
@media screen and (min-width: 768px) {
  .navigationItem:last-child {
    margin-right: 70px;
  }
}
@media screen and (min-width: 1280px) {
  .navigationItem:last-child {
    margin-right: 130px;
  }
}

.navigationLink {
  font-size: 12px;
  color: var(--main-text-color);
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.navigationLink:hover,
.navigationLink:focus {
  color: var(--btn-accent-color);
}

@media screen and (min-width: 768px) {
  .navigationLink {
    font-size: 14px;
  }
}
@media screen and (min-width: 1280px) {
  .navigationLink {
    font-size: 16px;
  }
}
.contactsList {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: var(--dark-bg-color);
}

.contactsItem {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 12px;
  margin-left: 10px;
}
.contactsItem:not(:first-child) {
  margin-left: 10px;
}
@media screen and (min-width: 768px) {
  .contactsItem {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
@media screen and (min-width: 1280px) {
  .contactsItem {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
.contactsLink {
  font-size: 12px;
  color: var(--main-text-color);
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.contactsLink:hover,
.contactsLink:focus {
  color: var(--btn-accent-color);
}
@media screen and (min-width: 768px) {
  .contactsLink {
    font-size: 14px;
  }
}
@media screen and (min-width: 1280px) {
  .contactsLink {
    font-size: 16px;
  }
}
